import React, { useState } from "react";
import { Card } from "react-bootstrap";
import ControlsMap from "../../../Properties/PropertiesConfiguration";
import SubformPropertiesJson from "../../../Properties/SubformPropertiesJson";
import styles from "../../../LeftNavigation/DesignerControls/BasicControls.module.css";
import CollapsibleComponent from "../../../../ReusableComponents/CollapseCard/CollapseCard";
import ColorPicker from "../../../../ReusableComponents/ColorPicker/ColorPicker";
import ControlLabel from "../../../../ReusableComponents/ControlLabel/ControlLabel";
import { regexValidator } from "../../../../Resources/globalFunctions";
import CustomizedAlert from "../../../../ReusableComponents/Alert/CustomizedAlert";
import ErrorMessageComponent from "../../../../ReusableComponents/ErrorMessageComponent/ErrorMessageComponent";
import { useTranslation } from "react-i18next";
import CreateTemplate from "../../../LeftNavigation/PageTemplate/CreatePageTemplate";
import axios from "../../../../axios";
import { getFormId } from "../../IFormDesignerLogic";
import { initialJSON } from "./SubformInitialJson";
import { cloneDeep } from "lodash-es";
import { GetStateFromData } from "../../../CustomHooks/CustomHooks";

function SubformProperties(props) {
  const translationStyles1 =
    window?.depsatkur_configs?.selectedLanguage === "ar" ||
    window?.depsatkur_configs?.selectedLanguage === "ar-SA";

  const { t } = useTranslation(["styles", "translation"]);

  const [alertData, setAlertData] = useState({
    open: false,
    showConfirm: "showNotification",
  });
  const [subformTemplates, setSubformTemplates] = useState([]);
  const [errorMessageId, setErrorMessageId] = useState(false);
  const [showTemplateBackdrop, setShowTemplateBackdrop] = useState({
    open: false,
    selectedTemplate: "None",
  });
  const [selectedTemplateType, setSelectedTemplateType] = useState("");
  function getTemplateList(type) {
    let payload = { withCredentials: true };
    if (type === "page") {
      axios
        .get("/templates/Templatetype/PAGE", payload)
        .then((response) => {
          setSelectedTemplateType(type);
          setSubformTemplates([...response.data]);
        })
        .catch((err) => console.log(err));
    } else if (type === "form") {
      axios
        .get("/templates/Templatetype/FORM", payload)
        .then((response) => {
          setSelectedTemplateType(type);
          setSubformTemplates([...response.data]);
        })
        .catch((err) => console.log(err));
    }
  }
  const contexts = GetStateFromData(props.containerId);
  const {
    updateJson,
    deleteControl,
    setSubFormState,
  } = contexts.contexts;
  const {
    updatedComponentInFormState,
  } = updateJson;

  function validateRegex(propertyName, propertyValue, event, inputBoxId) {
    try {
      let passValue = event?.key;
      if (propertyName === "CustomId") {
        passValue = [
          propertyValue.slice(0, event.target.selectionEnd) +
            event.key +
            propertyValue.slice(event.target.selectionEnd),
        ].join("");
      }
      if (
        event &&
        event.which !== 8 &&
        event.which !== 46 &&
        event.code !== "Tab" &&
        !regexValidator(passValue, ControlsMap[propertyName]["regexType"])
      ) {
        event.preventDefault();
        setAlertData({
          ...ControlsMap["customizedAlertNotificationData"],
          open: true,
          alertContentKey: "RegexErrorMessage",
        });
      } else {
        setErrorMessageId(false);
      }
    } catch (e) {
      console.log("Error while validating regex");
    }
  }

  function handleProperty(propertyName, propertyValue) {
    let temp_style = props.subformState.pageStyle;
    temp_style[propertyName] = propertyValue;
    props.subformState.pageStyle[propertyName] = propertyValue;
    updatedComponentInFormState(props.selectedControl.id, props.subformState);
  }

  let isTabPresent = false;
  const isTabPresentInsideJson = (child) => {
    if (Array.isArray(child) && !isTabPresent) {
      for (let item of child) {
        if (item.children) {
          if (item.elem_type === "tab") {
            isTabPresent = true;
            break;
          }
          isTabPresentInsideJson(item.children);
        }
      }
    } else if (child.children && !isTabPresent) {
      if (child.elem_type === "tab") {
        isTabPresent = true;
      }
      isTabPresentInsideJson(child.children);
    }
  };

  function handleClose() {
    setShowTemplateBackdrop((prev) => {
      return { ...prev, open: false };
    });
  }
  var CollapsibleItems = [];
  Object.keys(SubformPropertiesJson).forEach((key, index) => {
    var collapseComponentBody = [];
    Object.keys(SubformPropertiesJson[key]).forEach((property) => {
      collapseComponentBody.push(
        prepareHtmlTag(property, ControlsMap[property].type)
      );
    });
    if (CollapsibleItems === []) {
      CollapsibleItems.push(
        <CollapsibleComponent
          id={index + ""}
          heading={key}
          body={collapseComponentBody}
        />
      );
    }

    CollapsibleItems.push(
      <CollapsibleComponent
        id={index + ""}
        heading={key}
        body={collapseComponentBody}
      />
    );
  });

  function handleSelectTemplate(value) {
    try {
      if (value === "Select") {
        let formId = getFormId();
        var jsonToSend;
        if (
          props?.selectedControl?.id === "FormDef" &&
          props?.selectedControl?.elem_type === "FormDef"
        ) {
          jsonToSend = {
            populateInside: {
              frameIndex: window.buttonJson.selectedControl.frameIndex,
              elementIndex: window.buttonJson.selectedControl.elementIndex,
              sheetElementIndex:
                window.buttonJson.selectedControl.sheetElementIndex,
              sheetElementFrameIndex:
                window.buttonJson.selectedControl.sheetElementFrameIndex,
              sheetId: window.buttonJson.selectedControl.sheetIndex,
              stepIndex: window.buttonJson.selectedControl.stepIndex,
              subChildMenuIndex:
                window.buttonJson.selectedControl.subChildMenuIndex,
              subStepIndex: window.buttonJson.selectedControl.subStepIndex,
            },
            populateElem: initialJSON,
            prevSubform: window.buttonJson.selectedControl?.subform,
            formId: formId.toString(),
            //whatever the user is uploading will be this json
            //  }
          };
        } else {
          jsonToSend = {
            populateInside: {
              frameIndex: props.selectedControl.frameIndex,
              elementIndex: props.selectedControl.elementIndex,
              sheetElementIndex: props.selectedControl.sheetElementIndex,
              sheetElementFrameIndex:
                props.selectedControl.sheetElementFrameIndex,
              sheetId: props.selectedControl.sheetIndex,
              stepIndex: props.selectedControl.stepIndex,
              subChildMenuIndex: props.selectedControl.subChildMenuIndex,
              subStepIndex: props.selectedControl.subStepIndex,
            },
            populateElem: initialJSON,
            prevSubform: props.selectedControl?.subform,
            formId: formId.toString(),
            //whatever the user is uploading will be this json
            //  }
          };
        }

        axios
          .post("/idesign/populateSubformFromJson", jsonToSend, {
            withCredentials: true,
          })
          .then((response) => {
            setSubFormState(cloneDeep(initialJSON));
          });
      } else {
        axios
          .get(`/templates/${value}`, { withCredentials: true })
          .then((res) => {
            let templateBuffer = JSON.parse(res.data.templatebuffer);
            isTabPresentInsideJson(templateBuffer);
            if (
              isTabPresent ||
              templateBuffer?.containerControl === "wizard_h" ||
              templateBuffer?.containerControl === "wizard_v" ||
              templateBuffer?.containerControl === "sidenavigation" ||
              templateBuffer?.containerControl === "Menu"
            ) {
              setAlertData({
                showConfirm: "showNotification",
                open: true,
                alertContentKey:
                  "This template can't be associated as it contains either of these (Wizard/Menu/Tab) controls. Please select other template",
                alertType: "error",
                autoHideDuration: 4000,
              });
              return;
            }
            props.subformState.children[1].children = [
              ...templateBuffer.children[1].children,
            ];
            let formId = getFormId();
            props.subformState.selectedTemplateId = value;
            props.subformState.selectedTemplateType = selectedTemplateType;
            var jsonToSend;
            if (
              props?.selectedControl?.id === "FormDef" &&
              props?.selectedControl?.elem_type === "FormDef"
            ) {
              jsonToSend = {
                populateInside: {
                  frameIndex: window.buttonJson.selectedControl.frameIndex,
                  elementIndex: window.buttonJson.selectedControl.elementIndex,
                  sheetElementIndex:
                    window.buttonJson.selectedControl.sheetElementIndex,
                  sheetElementFrameIndex:
                    window.buttonJson.selectedControl.sheetElementFrameIndex,
                  sheetId: window.buttonJson.selectedControl.sheetIndex,
                  stepIndex: window.buttonJson.selectedControl.stepIndex,
                  subChildMenuIndex:
                    window.buttonJson.selectedControl.subChildMenuIndex,
                  subStepIndex: window.buttonJson.selectedControl.subStepIndex,
                },
                populateElem: props.subformState,
                prevSubform: window.buttonJson.selectedControl?.subform,
                formId: formId.toString(),
                //whatever the user is uploading will be this json
                //  }
              };
            } else {
              jsonToSend = {
                populateInside: {
                  frameIndex: props.selectedControl.frameIndex,
                  elementIndex: props.selectedControl.elementIndex,
                  sheetElementIndex: props.selectedControl.sheetElementIndex,
                  sheetElementFrameIndex:
                    props.selectedControl.sheetElementFrameIndex,
                  sheetId: props.selectedControl.sheetIndex,
                  stepIndex: props.selectedControl.stepIndex,
                  subChildMenuIndex: props.selectedControl.subChildMenuIndex,
                  subStepIndex: props.selectedControl.subStepIndex,
                },
                populateElem: props.subformState,
                prevSubform: props.selectedControl?.subform,
                formId: formId.toString(),
                //whatever the user is uploading will be this json
                //  }
              };
            }
            axios
              .post("/idesign/populateSubformFromJson", jsonToSend, {
                withCredentials: true,
              })
              .then((response) => {
                props.subformState.children[1].children = [
                  ...response.data.children[1].children,
                ];
                setSubFormState({ ...props.subformState });
              });
          })
          .catch((error) => {
            console.log("ERROR ON getting template formjson", error);
          });
      }
    } catch (error) {
      console.log(error);
    }
  }

  function prepareHtmlTag(key, type) {
    switch (type.toLowerCase()) {
      case "colorpicker":
        return (
          <div
            className={styles.parentDiv}
            style={translationStyles1 ? { display: "flex" } : null}
          >
            <span className={styles.leftLabel}>
              {t(`translation:${ControlsMap[key].label}`)}
            </span>
            <div className={styles.RightLabelColorPickerSubform}>
              <ColorPicker
                color={
                  props.subformState &&
                  props.subformState.pageStyle &&
                  props.subformState.pageStyle[key]
                }
                handleProperty={handleProperty}
                propertyName={key}
              />
            </div>
          </div>
        );
      case "subformtemplatebutton":
        return (
          <div
            className={styles.parentDiv}
            style={translationStyles1 ? { display: "flex" } : null}
          >
            <span className={styles.leftLabel}>
              {t(`translation:${ControlsMap[key].label}`)}
            </span>
          </div>
        );
      case "fullwidthtextinput":
        return (
          <div className={styles.textAreaSubform}>
            <span className={styles.leftLabel}>
              {t(`translation:${ControlsMap[key].label}`)}
            </span>
            <div>
              <input
                aria-label={"Control Value1"}
                type="text"
                maxLength={window?.depsatkur_configs?.label_max_length}
                defaultValue={
                  props.subformState &&
                  props.subformState.pageStyle &&
                  props.subformState.pageStyle["SubformTitle"]
                }
                className={styles.fullWidthInputTextSubformTitle}
                onKeyPress={(event) =>
                  validateRegex(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                onKeyUp={(event) =>
                  validateRegex(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                onBlur={(event) => handleProperty(key, event.target.value)}
              />
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );
      case "fullwidthtextinputgrey":
        return (
          <div className={styles.textAreaSubform}>
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <div>
                <input
                  aria-label={"Control Value2"}
                  type="radio"
                  id="page_Template"
                  name="pageTemplate"
                  value="page"
                  onChange={(e) => getTemplateList(e.target.value)}
                />
                <label
                  aria-label={"page Template"}
                  for="page_Template"
                  style={{ marginLeft: "5px" }}
                >
                  {t(`translation:Pages`)}
                </label>
              </div>
              <div>
                <input
                  aria-label={"Control Value3"}
                  type="radio"
                  id="page_Template"
                  name="pageTemplate"
                  value="form"
                  onChange={(e) => getTemplateList(e.target.value)}
                />
                <label
                  aria-label={"form Template"}
                  for="form_Template"
                  style={{ marginLeft: "5px" }}
                >
                  {t(`translation:Form`)}
                </label>
              </div>

              <br />
            </div>
            <select
              style={{ width: "95%" }}
              onChange={(e) => handleSelectTemplate(e.target.value)}
            >
              <option value={"Select"}>{t(`translation:Select`)}</option>
              {subformTemplates.map((template) => (
                <option value={template.templateid}>
                  {template.templatename}
                </option>
              ))}
            </select>
          </div>
        );
    }
  }
  return (
    <>
      <ControlLabel
        label="Subform"
        handleDelete={deleteControl}
        id={props?.subformState?.id}
      />
      <Card.Body className={styles.cardBodyButtonProperties}>
        {CollapsibleItems.map((item, index) => {
          return item;
        })}
        {showTemplateBackdrop.open === true ? (
          <>
            <CreateTemplate
              open={showTemplateBackdrop.open}
              componentFor={"subform"}
              handleClose={handleClose}
            />
          </>
        ) : null}
      </Card.Body>
      {alertData.open === true && <CustomizedAlert alertData={alertData} />}
    </>
  );
}

export default SubformProperties;
